import React, { Component } from 'react'
import $ from 'jquery'

export default class Download extends Component {

    componentDidMount() {
        window.scrollTo(0, 0)
        $("#main-nav").css({"background": "rgba(134, 153, 223, 0.85)"})
    }

    componentWillUnmount() {
        $("#main-nav").removeAttr("style")
    }

    render() {
        return(
            <div className="container" style={{paddingTop: "50px"}}>
                <div className="heading">
                    Download MTTV
                    <hr />
                </div>
                <div className="download-options row">
                    <div className="col-md-4">
                        <div className="card">
                            <div className="card-body d-flex flex-column text-center">
                                <div className="h5">Windows</div>
                                <a href="https://github.com/mttv/mttv-app/releases/download/v0.4.0/mttv-setup-0.4.0.exe" target="_blank" rel="noopener noreferrer" download className="btn btn-primary m-3 btn-download"><i className="fab fa-windows icon"></i>Download</a>
                                <small>Windows 7 or higher</small>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="card">
                            <div className="card-body d-flex flex-column text-center">
                                <div className="h5">Mac</div>
                                <a href="#" target="_blank" rel="noopener noreferrer" download className="btn btn-primary m-3 btn-download disabled"><i className="fab fa-apple icon"></i>Download</a>
                                <small>Mac OSX 10.10 or higher</small>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="card">
                            <div className="card-body d-flex flex-column text-center">
                                <div className="h5">Linux</div>
                                <a href="#" target="_blank" rel="noopener noreferrer" download className="btn btn-primary m-3 btn-download disabled"><i className="fab fa-linux icon"></i>Download</a>
                                <small>coming soon...</small>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}